.payment{
    background-color: white;
}

.payment-container>h1{
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234,237,237);
    border-radius: 1px solid lightgrey;
}

.payment-container>h1 a{
    text-decoration: none;
}

.payment-section{
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid lightgrey;
}

.payment-title{
    flex:0.2;
}

.payment-address,.payment-items,.payment-detail{
    flex:0.8;
}
.payment-detail>form{
    max-width: 400px;
}

.payment-detail>h3{
    padding-bottom: 20px;
}

.payment-detail>form>div>button{
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    font-weight: bolder;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.payment-detail>form>div>button:hover{
    cursor: pointer;
    background: #e5a911;

}