.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color: white;
}
.login-logo{
    margin-top: 70px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 200px;
    margin-right: auto;
    margin-left: auto;
}

.login-container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgrey;
    padding: 20px;

}

.login-container>h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login-container>form>h5{
    margin-bottom: 5px;
}

.login-container>form>input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login-container>p{
    margin-top: 15px;
    font-size:12px;
}

.signInButton{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}
.signInButton:hover{
    cursor: pointer;
    background: #e5a911;


}

.loginButton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgrey;
}
.loginButton:hover{
    cursor: pointer;
    background-color: darkgrey;
}
