.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    z-index: 1;
    background-color: white;
}
.addToBasket:hover{
    cursor: pointer;
    background-color: #e5a911;
}

.product-rating{
    display: flex;
}

.product>img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product>button{
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    align-items: center;
    border-radius: 3px;
    padding: 5px;
}
.product-price{
    margin-top: 5px;
}

.product-info{
    height: 100px;
    margin-bottom: 15px;
    @media (max-width:768px) {
        font-size: 12px;
        
    }
}